import React, {useState, createContext} from 'react';


//create context ===========================================
export const SwipeUDContext = createContext();


//create state ==============================================
export const SwipeUD = props => {
    const [swipeDown, setSwipeDown] = useState(true);



    return (
        <SwipeUDContext.Provider value = {[swipeDown, setSwipeDown]}>
            {props.children}
        </SwipeUDContext.Provider>
    );
}