import React, {Fragment, useContext} from 'react';

//Page Import ============================================================================
import Home from '../Pages/Home';
import About from '../Pages/About';
import Portfolio from '../Pages/Portfolio';
import Contact from '../Pages/Contact';

//Other Import ==========================================================================
import Nav from '../Nav/Nav';
import {PageContext} from '../Contexts/PageContext';
import {EnterExitContext} from '../Contexts/EnterExitContext';
import {SwipeLRContext} from '../Contexts/SwipeLRContext';
import {SwipeUDContext} from '../Contexts/SwipeUDContext';
// import { TransitionGroup, CSSTransition, Transition } from 'react-transition-group';
import posed from 'react-pose';



const TransitionLR = posed.div({
  enter: {
    opacity: 1,
    x: 0
  },
  exit: {
    opacity: 0,
    x: '200%'
  },
  transition: {type: 'physics', velocity: 1000},
})

const AppComp = () => {

    const [page, setPage] = useContext(PageContext);
    // console.log('THIS IS THE PAGE THATS IMPORTING FROM USE CONTEXT ', page )
    const [enterExit, setEnterExit] = useContext(EnterExitContext);
    const [swipeLeft, setSwipeLeft] = useContext(SwipeLRContext);
    const [swipeDown, setSwipeDown] = useContext(SwipeUDContext);


    switch (page) {
        case 'Home':
            return(
                <Fragment>
                <Nav />
                <TransitionLR pose={enterExit ? 'enter' : 'exit'}>
                <Home /> 
                </TransitionLR>
                </Fragment>
            )

        case 'About':
            return(
                <Fragment>
                <Nav />
                <TransitionLR pose={enterExit ? 'enter' : 'exit'}>
                  <About />
                </TransitionLR>
                </Fragment>
            )

        case 'Portfolio':
            return(
                <Fragment>
                <Nav />
                <TransitionLR pose={enterExit ? 'enter' : 'exit'}>
                  <Portfolio />
                </TransitionLR>
                </Fragment>
            )

        case 'Contact':
            return(
                <Fragment>
                <Nav />
                <TransitionLR pose={enterExit ? 'enter' : 'exit'}>
                  <Contact />
                </TransitionLR>
                </Fragment>
            )
    }


    return(
        <Fragment>
          <h1>{page}</h1>
        </Fragment>



    )
}

export default AppComp;