import React, {useState, createContext} from 'react';


//create context ===========================================
export const SwipeLRContext = createContext();


//create state ==============================================
export const SwipeLR = props => {
    const [swipeLeft, setSwipeLeft] = useState(true);



    return (
        <SwipeLRContext.Provider value = {[swipeLeft, setSwipeLeft]}>
            {props.children}
        </SwipeLRContext.Provider>
    );
}