import React, {useState, createContext} from 'react';


//create context ===========================================
export const EnterExitContext = createContext();


//create state ==============================================
export const EnterExit = props => {
    const [enterExit, setEnterExit] = useState(true);
    


    return (
        <EnterExitContext.Provider value = {[enterExit, setEnterExit]}>
            {props.children}
        </EnterExitContext.Provider>
    );
}